        0 8px 16px rgba(11, 31, 53, 0.16);
    --shadow-l-hard: 0 0 24px rgba(11, 31, 53, 0.12), 0 12px 24px rgba(11, 31, 53, 0.24),
        0 12px 24px rgba(11, 31, 53, 0.16);
