    --text-primary-color: var(--color-dark-indigo);
    --text-secondary-color: var(--color-dark-indigo-60-flat);
    --text-tertiary-color: var(--color-dark-indigo-30-flat);
    --border-radius: 4px;
    --focus-color: var(--color-blue-link-web);
    --font-family: system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Helvetica,
        sans-serif;
}
:root {
    --radio-hint-color: var(--color-dark-indigo-60-flat);
    --radio-label-color: var(--color-dark-indigo);
    --radio-bg-color: var(--color-dark-indigo-05-flat);
    --radio-border-color: var(--color-dark-indigo-60-flat);

    /* checked */
    --radio-checked-bg-color: var(--color-red-brand-85-flat);
    --radio-checked-border-color: var(--color-red-dark);

    /* disabled */
    --radio-disabled-bg-color: var(--color-dark-indigo-15-flat);

    /* disabled and checked */
    --radio-checked-disabled-bg-color: var(--color-red-brand-30-flat);

    /* hover */
    --radio-hover-bg-color: var(--color-dark-indigo-30-flat);
    --radio-hover-border-color: var(--color-dark-indigo-60-flat);
    --radio-checked-hover-bg-color: var(--color-red-brand);

    /* active */
    --radio-active-border-color: var(--color-dark-indigo);
}
.container {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    cursor: pointer
}
.container:not(.checked):not(.disabled):active .circle {
            background: #b6bcc3;
            background: var(--radio-hover-bg-color);
            border: 1px solid #0b1f35;
            border: 1px solid var(--radio-active-border-color);
        }
.container:not(.checked):not(.disabled) .circle {
            background: #f3f4f5;
            background: var(--radio-bg-color);
            border: 1px solid #6d7986;
            border: 1px solid var(--radio-border-color);
        }
.container:not(.checked):not(.disabled):hover:not(:active) .circle {
            background: #b6bcc3;
            background: var(--radio-hover-bg-color);
            border: 1px solid #6d7986;
            border: 1px solid var(--radio-hover-border-color);
        }
.container.disabled {
        cursor: default
    }
.container.disabled .circle {
            background: #dbdee1;
            background: var(--radio-disabled-bg-color);
            border: none;
        }
.container.checked:not(:active) .circle:before,
    .container.checked.disabled:active .circle:before {
        opacity: 1;
    }
.container.checked:active .circle {
            background: #ef3124;
            background: var(--color-red-brand);
            border: 1px solid #0b1f35;
            border: 1px solid var(--radio-active-border-color);
        }
.container.checked .circle {
            border: 1px solid #d91d0b;
            border: 1px solid var(--radio-checked-border-color);
            background-color: #f15045;
            background-color: var(--radio-checked-bg-color);
        }
.container.checked:hover .circle {
            background-color: #ef3124;
            background-color: var(--radio-checked-hover-bg-color);
        }
.container.focused .circle {
        outline: 2px solid #0f62fe;
        outline: 2px solid var(--focus-color);
        outline-offset: 2px;
    }
.container.disabled.checked .circle {
        background-color: #fbc2bd;
        background-color: var(--radio-checked-disabled-bg-color);
        border: none;
    }
.container input {
        position: absolute;
        z-index: -1;
        opacity: 0;
    }
.circle {
    width: 16px;
    height: 16px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 8px;
    position: relative;
    margin-right: 8px;
    margin-right: var(--gap-xs);
    margin-top: 4px;
    margin-top: var(--gap-2xs);
    -webkit-transition: background-color 0.2s ease, border 0.2s ease;
    transition: background-color 0.2s ease, border 0.2s ease
}
.circle:before {
        content: '';
        background-color: white;
        position: absolute;
        width: 6px;
        height: 6px;
        border-radius: 3px;
        margin: auto;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        -webkit-transition: opacity 0.2s ease;
        transition: opacity 0.2s ease;
        opacity: 0;
    }
.content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.label {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    color: #0b1f35;
    color: var(--radio-label-color);
}
.hint {
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    margin-top: 4px;
    margin-top: var(--gap-2xs);
    color: #6d7986;
    color: var(--radio-hint-color);
}
