:root {
    --skeleton-background-filter: none;
    --skeleton-default-color: var(--color-light-graphic-neutral);
}

.component {
    position: relative;
    display: inline-block;
    border-radius: 4px;
    overflow: hidden;
}

.component > * {
    visibility: hidden;
}

.component:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #dbdee1;
    background: var(--skeleton-default-color);
    -webkit-backdrop-filter: none;
            backdrop-filter: none;
    -webkit-backdrop-filter: var(--skeleton-background-filter);
            backdrop-filter: var(--skeleton-background-filter);
}

.animate:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 500%;
    height: 100%;
    -webkit-transform: translateX(-80%);
            transform: translateX(-80%);
    -webkit-animation-name: animation;
            animation-name: animation;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    background: -webkit-gradient(
        linear,
        right top, left top,
        from(rgba(0, 0, 0, 0)),
        color-stop(40%, rgba(0, 0, 0, 0)),
        color-stop(50%, rgba(0, 0, 0, 0.025)),
        color-stop(60%, rgba(0, 0, 0, 0)),
        to(rgba(0, 0, 0, 0))
    );
    background: linear-gradient(
        to left,
        rgba(0, 0, 0, 0),
        rgba(0, 0, 0, 0) 40%,
        rgba(0, 0, 0, 0.025) 50%,
        rgba(0, 0, 0, 0) 60%,
        rgba(0, 0, 0, 0)
    );
}

@-webkit-keyframes animation {
    0% {
        -webkit-transform: translateX(-80%);
                transform: translateX(-80%);
    }
    67% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes animation {
    0% {
        -webkit-transform: translateX(-80%);
                transform: translateX(-80%);
    }
    67% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}
