:root {
    --font-family-system: system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Helvetica,
        sans-serif;
    --font-family-styrene: 'Styrene UI', system-ui, -apple-system, 'Segoe UI', Roboto,
        'Helvetica Neue', Helvetica, sans-serif;
}
.tool {
    margin-top: auto;
    margin-bottom: auto;
}
.label {
    display: inline-block;
    margin-right: 8px;
    font-size: 13px;
}
.click {
    --amount-major-part-font-weight: bold;
    --amount-minor-part-font-weight: bold;
    /* core vars */
    --color-light-specialbg-component: #eeeff1;
    --gap-s: 12px;
    --gap-m: 16px;
    /* theme */
    --form-control-border-radius: 4px;
    --form-control-border-bottom: 0;
    --form-control-bg-color: var(--color-light-specialbg-component);
    /* paddings */
    --form-control-paddings: 0 var(--gap-s);
    --form-control-l-paddings: 0 var(--gap-m);
    --form-control-labeled-s-paddings: 23px var(--gap-s) 5px;
    --form-control-labeled-m-paddings: 28px var(--gap-s) 8px;
    --form-control-labeled-l-paddings: 34px var(--gap-m) 14px;
    /* disabled */
    --form-control-disabled-border-bottom: 0;
    --form-control-disabled-bg-color: var(--color-light-specialbg-component);
    /* focus */
    --form-control-focus-shadow: none;
    --form-control-focus-border-bottom: 0;
    --form-control-hover-bg-color: var(--color-light-specialbg-component);
    /* error */
    --form-control-error-shadow: inset 0 0 0 1px var(--form-control-error-color);
    --form-control-error-border-bottom: 0;
    --form-control-error-icon-display: none;
    /* core vars */
    --color-light-specialbg-component: #eeeff1;
    --gap-s: 12px;
    --gap-m: 16px;
    /* theme */
    --pure-input-border-radius: 4px;
    --pure-input-border-bottom: 0;
    --pure-input-bg-color: var(--color-light-specialbg-component);
    /* paddings */
    --pure-input-paddings: 0 var(--gap-s);
    --pure-input-l-paddings: 0 var(--gap-m);
    /* disabled */
    --pure-input-disabled-border-bottom: 0;
    --pure-input-disabled-bg-color: var(--color-light-specialbg-component);
    /* focus */
    --pure-input-focus-shadow: none;
    --pure-input-focus-border-bottom: 0;
    --pure-input-hover-bg-color: var(--color-light-specialbg-component);
    /* core vars */
    --color-light-bg-accent: #ef3124;
    --color-light-bg-neutral: #dbdee1;
    /* primary */
    --button-primary-base-bg-color: var(--color-light-bg-accent);
    --button-primary-hover-bg-color: rgb(215, 44, 32);
    --button-primary-active-bg-color: rgb(167, 34, 25);
    --button-primary-disabled-bg-color: rgba(239, 49, 36, 0.3);
    /* secondary */
    --button-secondary-base-bg-color: var(--color-light-bg-neutral);
    --button-secondary-hover-bg-color: rgb(197, 200, 203);
    --button-secondary-active-bg-color: rgb(153, 155, 157);
    --button-secondary-disabled-bg-color: rgba(219, 222, 225, 0.3);
    /* core vars */
    --font-family-styrene: 'Styrene UI', system-ui, -apple-system, 'Segoe UI', Roboto,
        'Helvetica Neue', Helvetica, sans-serif;
    /* theme */
    --primary-tablist-s-font-size: 20px;
    --primary-tablist-s-font-weight: 500;
    --primary-tablist-s-font-family: var(--font-family-styrene);
    --primary-tablist-m-font-size: 20px;
    --primary-tablist-m-font-weight: 500;
    --primary-tablist-m-font-family: var(--font-family-styrene);
    --primary-tablist-l-font-size: 24px;
    --primary-tablist-l-font-weight: 500;
    --primary-tablist-l-font-family: var(--font-family-styrene);
    /* core vars */
    --color-light-specialbg-component: #eeeff1;
    --color-white: #fff;
    /* theme */
    --tag-background-color: var(--color-light-specialbg-component);
    --tag-background-color-hover: rgb(214, 215, 217);
    --tag-background-color-active: rgb(167, 167, 169);
    --tag-text-color-checked-hover: rgb(179, 179, 179);
    --tag-border-color: transparent;
    --tag-border-color-hover: transparent;
    --tag-border-color-active: transparent;
    --tag-border-color-disabled: transparent;
    /* core vars */
    --color-light-text-primary: #0b1f35;
    --color-light-text-secondary: #6d7986;
    --color-light-text-link: #007aff;
    /* primary */
    --link-primary-hover-color: rgb(84, 98, 114);
    --link-primary-active-color: rgb(133, 143, 154);
    /* secondary */
    --link-secondary-hover-color: rgb(153, 161, 170);
    --link-secondary-active-color: rgb(182, 188, 195);
    /* default */
    --link-default-hover-color: rgb(77, 162, 255);
    --link-default-active-color: rgb(128, 189, 255);
}
.site {
    /* core vars */
    --gap-4xl: 48px;
    /* theme */
    --primary-tablist-m-gaps: var(--gap-4xl);
    --primary-tablist-m-font-weight: 500;
    --primary-tablist-l-gaps: var(--gap-4xl);
    --primary-tablist-l-font-weight: 500;
}
.corp {
    /* core vars */
    --color-light-border-key: #0b1f35;
    --color-light-specialbg-component: #eeeff1;
    /* secondary */
    --button-secondary-base-bg-color: transparent;
    --button-secondary-base-border-color: rgba(11, 31, 53, 0.6);
    --button-secondary-hover-bg-color: rgb(221, 222, 224);
    --button-secondary-active-bg-color: rgb(202, 203, 205);
    --button-secondary-disabled-bg-color: transparent;
    --button-secondary-disabled-border-color: rgba(11, 31, 53, 0.1);
}
.mobile {
    /* core vars */
    --color-light-bg-secondary: #f3f4f5;
    /* theme */
    --button-border-radius: 12px;
    /* primary TODO secret color from the future */
    --button-primary-base-bg-color: #2b2b2b;
    --button-primary-hover-bg-color: rgb(39, 39, 39);
    --button-primary-active-bg-color: rgb(30, 30, 30);
    --button-primary-disabled-bg-color: rgba(43, 43, 43, 0.3);
    /* secondary */
    --button-secondary-base-bg-color: var(--color-light-bg-secondary);
    --button-secondary-hover-bg-color: rgb(219, 220, 221);
    --button-secondary-active-bg-color: rgb(170, 171, 172);
    --button-secondary-disabled-bg-color: rgba(243, 244, 245, 0.3);
}
