    --focus-color: var(--color-blue-link-web);
    --font-family: system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Helvetica,
        sans-serif;
}
.component {
    /* TODO:        webView */
    --form-control-border-radius: 0;
    --form-control-border-bottom: 1px solid var(--color-black-60);
    --form-control-bg-color: transparent;

    /* focus */
    --form-control-focus-shadow: none;
    --form-control-focus-border-bottom: 1px solid var(--color-black);

    /* hover */
    --form-control-hover-bg-color: transparent;
    --form-control-font-family: var(--font-family-styrene);

    position: relative;
    max-width: 343px;

    font-family: 'Styrene UI', system-ui, -apple-system, 'Segoe UI', Roboto,
        'Helvetica Neue', Helvetica, sans-serif;

    font-family: var(--font-family-styrene)
}
.component:not(.focused):not(.filled) .label {
        color: rgba(0, 0, 0, 0.6);
        color: var(--color-black-60);
        -webkit-transform: translateY(12px);
                transform: translateY(12px);
    }
.aspectRatioContainer {
    /*   343x215 */
    padding-bottom: 62.6%;
}
.content {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 12px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.label {
    color: #000;
    color: var(--color-black);
    left: 0;
    top: -4px;
}
.focused .label,
.filled .label {
    font-size: 16px;
    -webkit-transform: translateY(-16px) scale(0.75);
            transform: translateY(-16px) scale(0.75);
}
.input {
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    -webkit-font-feature-settings: 'ss01';
            font-feature-settings: 'ss01';
    font-family: 'Styrene UI', system-ui, -apple-system, 'Segoe UI', Roboto,
        'Helvetica Neue', Helvetica, sans-serif;
    font-family: var(--font-family-styrene);

    color: #000;

    color: var(--color-black);
    caret-color: #000;
    caret-color: var(--color-black);

    /* TODO:   ,     -,     form-control */
    padding-left: 0 !important
}
