.usePhoto {
    display: block;
    margin: 0;
    padding: 0;
    text-decoration: none;
    background-color: transparent;
    border: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    outline: none;
    margin-bottom: 4px;
    -ms-flex-item-align: end;
        align-self: flex-end;

    /*        ,   -     */
    margin-right: calc(12px * -1);
