.component {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    margin: 0;
    padding: 0;
    border: 0;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

    /* TODO default outline and themeable focus styles */
    outline: none;
}
.component input {
    opacity: 0;
    position: absolute;
}
.switch {
    position: relative;
    border-radius: 16px;
    width: 36px;
    height: 20px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    background-color: #b6bcc3;
    background-color: var(--switch-bg-color);
    border: 2px solid #b6bcc3;
    border: 2px solid var(--switch-border-color);
    -webkit-transition: background-color 0.2s ease, border-color 0.2s ease;
    transition: background-color 0.2s ease, border-color 0.2s ease;
}
.switch:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #fff;
    border: 0.5px solid rgba(11, 31, 53, 0.05);
    border: 0.5px solid var(--color-dark-indigo-05);
    -webkit-box-shadow: 0 2px 4px rgba(11, 31, 53, 0.3);
            box-shadow: 0 2px 4px rgba(11, 31, 53, 0.3);
    -webkit-box-shadow: 0 2px 4px var(--color-dark-indigo-30);
            box-shadow: 0 2px 4px var(--color-dark-indigo-30);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: -webkit-transform 0.2s ease;
    transition: -webkit-transform 0.2s ease;
    transition: transform 0.2s ease;
    transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}
.content {
    margin-left: 16px;
    margin-left: var(--gap-m);
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}
.label {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    display: block;
    color: #0b1f35;
    color: var(--switch-label-color);
}
.label:not(:only-child) {
    margin-bottom: 4px;
    margin-bottom: var(--gap-2xs);
}
.hint {
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    display: block;
    color: #6d7986;
    color: var(--switch-hint-color);
}
/* Reversed state */
.component.reversed {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
        -ms-flex-direction: row-reverse;
            flex-direction: row-reverse;
}
.reversed .content {
    margin-right: 16px;
    margin-right: var(--gap-m);
    margin-left: 0;
}
/* Checked state */
.checked .switch {
    background-color: #f15045;
    background-color: var(--switch-checked-color);
    border-color: #f15045;
    border-color: var(--switch-checked-border-color);
}
.checked .switch:before {
    /*  (36px + 2*2px) - (2 * 2px) -  (20px) */
    -webkit-transform: translateX(16px);
            transform: translateX(16px);
}
/* Disabled state */
.disabled {
    cursor: auto;
    cursor: initial;
}
.disabled .switch {
    background-color: #dbdee1;
    background-color: var(--switch-disabled-color);
    border-color: #dbdee1;
    border-color: var(--switch-disabled-border-color);
}
.disabled .switch:before {
    -webkit-box-shadow: none;
            box-shadow: none;
}
.disabled.checked .switch {
    background-color: #fbc2bd;
    background-color: var(--switch-disabled-checked-color);
    border-color: #fbc2bd;
    border-color: var(--switch-disabled-checked-border-color);
}
/* Focused state */
.focused .switch {
    outline: 2px solid #0f62fe;
    outline: 2px solid var(--focus-color);
    outline-offset: 2px;
}
