:root {
    --loader-animation-duration: 750ms;
}

.component {
    fill: currentColor
}

.component circle {
        -webkit-animation-name: blink;
                animation-name: blink;
        -webkit-animation-duration: 750ms;
                animation-duration: 750ms;
        -webkit-animation-duration: var(--loader-animation-duration);
                animation-duration: var(--loader-animation-duration);
        -webkit-animation-iteration-count: infinite;
                animation-iteration-count: infinite;
        -webkit-animation-timing-function: ease-out;
                animation-timing-function: ease-out;
    }

.component circle:nth-child(1) {
        -webkit-animation-delay: 0ms;
                animation-delay: 0ms;
    }

.component circle:nth-child(2) {
        -webkit-animation-delay: calc(750ms / 3);
                animation-delay: calc(750ms / 3);
        -webkit-animation-delay: calc(var(--loader-animation-duration) / 3);
                animation-delay: calc(var(--loader-animation-duration) / 3);
    }

.component circle:nth-child(3) {
        -webkit-animation-delay: calc(750ms * 2 / 3);
                animation-delay: calc(750ms * 2 / 3);
        -webkit-animation-delay: calc(var(--loader-animation-duration) * 2 / 3);
                animation-delay: calc(var(--loader-animation-duration) * 2 / 3);
    }

@-webkit-keyframes blink {
    0% {
        opacity: 0.2;
    }

    60% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
    }
}

@keyframes blink {
    0% {
        opacity: 0.2;
    }

    60% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
    }
}
